.detectHeading{
    top: 13vh;
    text-align: center;
    position: relative;
    color: #16F7FA;
    font-size: 4vh;
    margin: 0;
    padding: 0;
}

.background1{
    background-image: linear-gradient(#004A91, #010207);
    background-size: cover;
    height: 100%;    
    
    position: relative;
    text-align: center;
}


.button{
    background-color: #4ECCA3;
    height: 3vh;
    transition-duration: 0.4s;
    width: 9vw;
    border-radius: 1.5vh;
    position: relative;
    margin: auto;
    font-weight: bold;
    font-size: 1.3vw;
    display: inline-block;
    vertical-align: middle;
    text-align: center;
    border: 2px solid #4ECCA3;
}

.button:hover{
    background-color: white;
}